import React, { useState } from "react";
import AddIcon from '@mui/icons-material/Add';
import Fab from '@mui/material/Fab';
import Zoom from '@mui/material/Zoom';

function CreateArea(props) {
    const[note, setNote] = useState({
        title:"",
        content:""
    })

    const [isExpand, setExpand] = useState(false);


    function handleChange(event){
        const {name,value} = event.target;
        setNote((preNote)=>{
            return {
                ...preNote, [name]:value
            }
        })

    }

    function submitNote(event){
        props.onAdd(note);
        event.preventDefault();
        setNote({
            title:"",
            content:""
        })
    }

    function expand(){
      setExpand(true);
    }

  return (
    <div>
      <form className="create-note" >
      {isExpand ? <input name="title" placeholder="Title" onChange={handleChange} value={note.title} /> : null}
        {/* <input name="title" placeholder="Title" onChange={handleChange} value={note.title}/> */}
        <textarea name="content" placeholder="Take a note..." rows={isExpand ? 3 : 1} onChange={handleChange} value={note.content} onClick={expand}/>
        <Zoom in={isExpand}>
        <Fab onClick={submitNote}><AddIcon /></Fab>
        </Zoom>
      </form>
    </div>
  );
}

export default CreateArea;
